import { createSlice, PayloadAction } from "@reduxjs/toolkit";
import { calcTotalPrice } from "../../utils/calcTotalPrice";
import { getFromLocStor } from "../../utils/getFromLocStor";
import { CartSliceState, CartItemSlice } from "./types";


const {items, totalPrice} = getFromLocStor();//   localstorage

const initialState: CartSliceState = {
  totalPrice,
  items,
};

export const cartSlice = createSlice({
  name: "cart",
  initialState,
  reducers: {
    addItem(state, action: PayloadAction<CartItemSlice>) {
      const findItem = state.items.find((obj) => obj.id === action.payload.id); //     id    
      if (findItem) {
        findItem.count++; //
      } else {
        state.items.push({ ...action.payload, count: 1 }); //  items.count
      }

      state.totalPrice = state.items.reduce((sum, obj) => {
        //     
        return obj.price * obj.count + sum;
      }, 0);
    },
    minusItem(state, action: PayloadAction<string>) {
      const findItem = state.items.find((obj) => obj.id === action.payload);
      if (findItem) {
        findItem.count--;
      }

      state.totalPrice = calcTotalPrice(state.items);
    },
    removeItem(state, action: PayloadAction<string>) {
      //  
      state.items = state.items.filter((obj) => obj.id !== action.payload);

      state.totalPrice = state.items.reduce((sum, obj) => {
        //     
        return obj.price * obj.count + sum;
      }, 0);
    },
    clearItem(state) {
      //    
      state.items = [];
      state.totalPrice = 0;
    },
  },
});

// Action creators are generated for each case reducer function
export const { addItem, removeItem, clearItem, minusItem } = cartSlice.actions;

export default cartSlice.reducer;
